.side-bar-header-title-container {
  display: flex;
  padding: 24px;
  color: white;
  font-weight: bold;
  justify-content: center;
  border-bottom: 1px solid gray;
  align-items: center;
  font-size: 20px;
  // background-color: rgb(45, 73, 167);
}

.search-icon {
  position: absolute;
  left: 22px;
  top: 17px;
  height: 16px;
  width: 16px;
  fill: white;
}

.side-bar-header-container {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  display: flex;
  // gap: 24px;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-weight: bold;
  padding: 0px 24px;
}

.side-bar-category {
  margin-top: 4px;
  padding: 8px 12px;
  filter: brightness(0.5);
}

.side-bar-category:hover {
  cursor: pointer;
  // filter: brightness(0.8);
}

.side-bar-category-active {
  border-bottom: 1px solid rgb(81, 81, 197);
  filter: brightness(1);
}

.side-bar-category-active:hover {
  cursor: default;
}

