.side-bar-container-generate-button {
  padding: 24px 0px;
  width: 100%;
  background-color: rgb(59, 88, 180);
  color: white;
  border: 0px;
  border-top: 1px solid gray;
  font-size: 16px;
  transition: all 300ms;
  font-weight: bold;

  &:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}
